<template>
	<view class="get">
		<view class="get-list" v-if="!isEmpty">
			<view v-for="(item, index) in tableData" :key="index" class="row">
				<view class="carrier">
					<view class="get-wrapper" @click="toDetail(item)">
						<view class="title in1line">
							打卡状态:  {{item.status === '0' ? '正常' : '异常'}}
						</view>
						<view class="title in1line">
							<template>打卡时间:  {{item.createTime}}</template>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<view class="helperList" v-if="isEmpty">
				<oa-empty info="暂无数据"></oa-empty>
		</view>

		<view class="add-round bg-deepBlue" :class="'bg-' + themeColor.name" @click="navToAdd()">
			<text class="cuIcon-add text-xxl"></text>
		</view>
		<!--加载动画-->
	</view>

</template>

<script>
	import uniCalendar from '@/components/oa-calendar/oa-calendar.vue'
	import moment from '@/common/moment';
	import oaEmpty from '@/components/oa-empty';
	import requests from '@/utils/requests.js'
	export default {
		components: {
			uniCalendar,
			oaEmpty
		},
		data() {
			return {
				isEmpty: false,
				today: undefined,
				selectdate: undefined,
				startTime: moment().startOf('day').format('YYYY-MM-DD'),
				recordList: [],
				// 控制滑动效果
				theIndex: null,
				oldIndex: null,
				tableData: [],
				searchParameters: {
					pageNum: 1,
					pageSize: 50,
					userId: uni.getStorageSync('userInfo').userId,
				},
			};
		},
		filters: {
			time(val) {
				return moment(val * 1000).format('YYYY-MM-DD HH:mm:ss');
			},

		},
		onShow() {
			this.initData();
		},
		// 下拉刷新
		onPullDownRefresh() {
			// this.page = 1;
			// this.getList = [];
			// this.recordList = [];
			// this.loading = true;
			//this.getRecordList('refresh', this.selectdate);
		},
		// 加载更多
		onReachBottom() {
			// if (this.loadingType === 'nomore') return;
			// this.page++;
			//this.getRecordList('', this.selectdate);
		},
		async onLoad(options) {},
		methods: {
			change(e) {
				if (e.type !== 0) this.selectdate = e.fulldate;
				else this.selectdate = '9999';
				//this.getRecordList('refresh', this.selectdate);
			},
			// 数据初始化
			initData() {
				this.hasLogin = this.$mStore.getters.hasLogin;
				// this.page = 1;
				// this.recordList.length = 0;
				// this.getList.length = 0;
				//this.getRecordList('', this.selectdate);
				this.getList();
				uni.setNavigationBarColor({
					frontColor: '#ffffff',
					backgroundColor: this.themeColor.color,
					animation: {
						duration: 400,
						timingFunc: 'easeIn'
					}
				})
			},
			// 获取记录列表
			getList() {
				let that = this
				requests.getSignInRecordList(this.searchParameters).then(res => {
					console.log('getList===>',res)
					that.tableData = res.rows;
					if(that.tableData.length === 0){
						that.isEmpty = true;
					}else{
						that.isEmpty = false;
					}
				})
			},
			
			// 详情
			toDetail(val){
				console.log(val)
				uni.navigateTo({
					url: './add?type=' + 2 + '&obj=' + JSON.stringify(val)
				})
			},
			
			// 打卡
			navToAdd() {
				uni.navigateTo({
					url: './add?type=' + 1
				})
			},
		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.get {

		.add-round {
			position: fixed;
			z-index: 999;
			right: 30rpx;
			bottom: 30rpx;
			border-radius: 50%;
			width: 120rpx;
			height: 120rpx;
			color: #fff;
			text-align: center;
			line-height: 120rpx;
			font-weight: 100;
			font-size: 80rpx;
		}

		.get-list {
			margin-top: 20upx;

			.row {
				width: calc(94%);
				height: calc(22vw + 40upx);
				margin: 0 auto $spacing-base;
				border-radius: 15upx;
				box-shadow: 0upx 5upx 20upx rgba(0, 0, 0, 0.1);
				display: flex;
				align-items: center;
				position: relative;
				overflow: hidden;
				z-index: 4;
				border: 0;

				.carrier {
					@keyframes showMenu {
						0% {
							transform: translateX(0);
						}

						100% {
							transform: translateX(-30%);
						}
					}

					@keyframes closeMenu {
						0% {
							transform: translateX(-30%);
						}

						100% {
							transform: translateX(0);
						}
					}

					background-color: #fff;
					position: absolute;
					width: 100%;
					padding: 0 0;
					height: 100%;
					z-index: 3;
					display: flex;
					align-items: center;
				}
			}

			.get-wrapper {
				background-color: $color-white;
				width: 100%;
				padding: $spacing-base $spacing-lg $spacing-base $spacing-lg;
				margin: $spacing-base 0 0;
				border-radius: 15upx;
				position: relative;

				.title {
					font-size: $font-lg;
					color: $font-color-dark;
					font-weight: 500;
					margin: 0 0 $spacing-sm;
				}

				.content {
					font-size: $font-sm;
					color: $font-color-base;
					height: 75upx;
					line-height: 36upx;
				}

				.time {
					font-size: $font-base;
					color: $font-color-light;
				}

			}
		}

	}
</style>
